import {signup} from '../actions/useractions'
import  Validator from 'validator'
import { useState} from 'react'
import {useDispatch} from 'react-redux'
const { default: Header } = require("../components/headers")

const SignupScreen = (props)=>{


    const [firstname,setFirstname]=useState('');
    const [lastname,setLastname]=useState('');
    const [email,setEmail]=useState('');
    const [phone,setPhone]=useState('');
    const [address,setAddress]=useState('');
    const [password,setPassword]=useState('');
    const [confirmpasswor,setConfirmPassword]=useState('');
const dispatch = useDispatch()


function validate(IDofInput,IDofErrordiv,errormsg){
const IDofInpu = document.getElementById(IDofInput)
const errorDiv = document.getElementById(IDofErrordiv)


if(IDofInpu.value !== ''){
  errorDiv.innerHTML=''
  return true
}
else{
  errorDiv.innerHTML = errormsg
  return false
}
}


const onSignup = ()=>{
  if(validate('firstname','firstnameDiv','First name is mandatory....') &&
   validate('lastname','lastnameDiv','Last naem is mandatory...!')&&
   validate('email','emailDiv','Email is mandatory...!')&&
   validate('phone','phoneDiv','Phone is manadatory')&&
   validate('address','addressDiv','Address is required')&&
   validate('password','passwordDiv','Enter password')&&
   validate('confirmpassword','confirmpasswordDiv','Enter password')){
    const verify = document.getElementById('passwordverify')
     if(confirmpasswor.length <= 6){
        verify.innerHTML='Password length should be at leat 8 digits '
    
        if(confirmpasswor !== password){
          verify.innerHTML='Password does not matching'} 
    }
     else{
    verify.innerHTML=''
dispatch(signup(firstname,lastname,email,phone,address,password))
  }
  
    const emailverify = document.getElementById('emailDiv')
   if (!Validator.isEmail(email))
    {
      emailverify.innerHTML = 'enter valid email'
      return true
  
    }
     else{
       emailverify.innerHTML = ''
      return false
  
     }
    
  }
 
  
}
const onSignin=()=>{
  props.history.push('/signin')
}

    return(
        <div>
          
    <div className="container">

      <div className="row">
          
        <div className="col-md-8 offset-md-2">
          <div className="login-form">
          <Header title="Sign Up"></Header>
          <div className="form-group">
                <label >First Name</label>
                <input id="firstname" onChange={(e)=>{
                    setFirstname(e.target.value)
                }} type="text" className="form-control"   placeholder="Enter Your First Name"/>
                <div id="firstnameDiv"  className="form-text text-muted"></div>
              </div>
              <div className="form-group">
                <label >Last Name</label>
                <input id="lastname" onChange={(e)=>{
                    setLastname(e.target.value)
                }} type="text" className="form-control"   placeholder="Enter Your Last Name"/>
                <div className="errordiv" id="lastnameDiv"  className="form-text text-muted"></div>
              </div>
              <div className="form-group">
                <label >Email address</label>
                <input id="email" onChange={(e)=>{
                    setEmail(e.target.value)
                }} type="email" className="form-control"   placeholder="Enter email"/>
                <div className="errordiv" id="emailDiv"  className="form-text text-muted"></div>
              </div>
              <div className="form-group">
                <label >Phone</label>
                <input id="phone" onChange={(e)=>{
                    setPhone(e.target.value)
                }} type="phone" className="form-control"   placeholder="Enter Your Phone number with country code"/>
                <div  className="errordiv" id="phoneDiv" className="form-text text-muted"></div>
              </div>
              <div className="form-group">
                <label >Address</label>
                <input id="address" onChange={(e)=>{
                    setAddress(e.target.value)
                }} type="text" className="form-control"   placeholder="Enter Your Address : city/town, tal, dist, state, country "/>
                <div className="errordiv" id="addressDiv" className="form-text text-muted"></div>
              </div>
              <div className="form-group">
                <label >Password</label>
                <input id="password" onChange={(e)=>{
                    setPassword(e.target.value)
                }} type="password" className="form-control" placeholder="Password"/> 
           <div className="errordiv" id="passwordDiv"  className="form-text text-muted"></div>
              </div>
              <div className="form-group">
                <label >Confirm Password</label>
                <input id="confirmpassword" onChange={(e)=>{
                    setConfirmPassword(e.target.value)
                }} type="password" className="form-control"  placeholder="Password"/>
           <div className="errordiv" id="confirmpasswordDiv"  className="form-text text-muted"></div>
           <div className="errordiv" id="passwordverify"></div>
              </div>
              <div className="form-group form-check">
                <input type="checkbox" className="form-check-input" />
                <label className="form-check-label" >Check me out</label>
              </div>
              <button onClick={onSignup} type="submit" className="btn btn-primary">Submit</button>
              <button onClick={onSignin} className="btn btn-success float-end">Signin</button>
         <div className="float-end">  <span className="already">      Already have account!!!?</span>
</div>
          </div>
        </div>
      </div>
    </div>

    
  
 
  
</div>





       
    )
     }
    
    
    
     export default SignupScreen